.calc-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 40px;
    padding: 10px;
}

form {
    display: flex;
    flex-direction: column;
    height: 100px;
    align-items: center;
}

label {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

input {
    margin: 10px;
}

@media screen and (max-width: 460px) {
    .calc-content {
        width: 100vw;
        margin: 0;
        padding: 0;
    }
}
